import React, { Component } from 'react';
import { connect } from "react-redux";
import { NavLink, Link } from 'react-router-dom'
import { baseUrl } from '../../Assets/AssetsHelper';
import SVG from 'react-inlinesvg';
import { Collapse } from 'react-bootstrap';
import { useLocation } from '../../Services/Utils/utils';
import { resetFilter } from '../../Services/Actions/assetsActions'

class AsideMenu extends Component {
    constructor() {
        super();
        this.state = {
            support: false
        };
    }
    submenuDropdown = (val) => {
        if (val === 'collections') {
            this.setState({
                support: false
            })
        } else {
            this.setState({
                support: !this.state.support
            })
        }
    }
    closeAsideToggle = (val, assetsStatus) => {
        if (val === 'main') {
            this.setState({
                support: false
            })
        } else if (val === 'child-menu') {
            this.setState({
                support: false
            })
        }
        var rootEle = document.getElementById('ugc-suit-main');
        if (rootEle) {
            rootEle.classList.remove('aside-on');
        }
        if (assetsStatus && String(window.location.href).includes("assets")) {
            this.props.resetFilter()
            const overlay = document.querySelector(".offcanvas-backdrop")
            if (overlay) {
                overlay.click()
            }
        }
    }
    menuActiveClass = (menuLink) => {
        try {
            if (window.location.href && String(window.location.href).includes(menuLink)) return 'active'
            else return ''
        }
        catch (ex) {
            return ''
        }
    }


    render() {
        const { support } = this.state;
        const { menuActiveClass } = this
        return <div className="sidebar-menu">
            <ul className="sidebar-menu-list list-unstyled mb-0 pe-3 pe-xxl-4 ps-2">
                <li className="sidebar-menu-items">
                    <Link to="/home" className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("home")}`} activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/home.svg')} />
                        </span>
                        <span className="menu-text">Dashboard</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/assets" className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("assets")}`} exact={useLocation} activeclassname="active" onClick={() => this.closeAsideToggle('main', true)}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/image.svg')} />
                        </span>
                        <span className="menu-text">UGC Library</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/manage-connection" className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("manage-connection")} ${menuActiveClass("social-account")}`} activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/connection.svg')} />
                        </span>
                        <span className="menu-text">Network Manager</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/template" className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("template")}`} activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/layout.svg')} />
                        </span>
                        <span className="menu-text">Templates</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/rule" className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("rule")}`} onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/file-edit.svg')} />
                        </span>
                        <span className="menu-text">AutoPilot</span>
                    </Link >
                </li>
            </ul>
            <hr className="my-4" />
            <ul className="sidebar-menu-list list-unstyled mb-0 pe-3 pe-xxl-4 ps-2">
                <li className="sidebar-menu-items">
                    <Link to="/analytics" className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("analytics")}`} onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/graph.svg')} />
                        </span>
                        <span className="menu-text">Insights</span>
                    </Link >
                </li>
                <li className={`sidebar-menu-items ${support ? 'active' : ''}`}>
                    <span className="menu-toggle nav-menu-item d-flex align-items-center fw-bold text-decoration-none justify-content-between" onClick={() => this.submenuDropdown('support')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/question.svg')} />
                        </span>
                        <span className="menu-text flex-grow-1">Help Center</span>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" height="12" width="12" className="menu-arrow text-gray-800">
                            <path fill="none" d="M0 0H24V24H0z" />
                            <path d="M278.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-160 160c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L210.7 256 73.4 118.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l160 160z" fill="currentColor" />
                        </svg>
                    </span>
                    <div className="menu-submenu">
                        <Collapse in={support}>
                            <ul className="submenu-nav list-unstyled mb-0">
                                <li className="submenu-nav-item mt-2">
                                    <Link className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active"
                                        //onClick={() => this.closeAsideToggle('child-menu')} 
                                        onClick={window.Intercom ? (e) => { window.Intercom('show') } : () => { }}>
                                        <span className="menu-icon">
                                            <SVG src={baseUrl('media/icons/svg-icons/chat.svg')} />
                                        </span>
                                        <span className="menu-text">Chat</span>
                                    </Link >
                                </li>
                                <li className="submenu-nav-item">
                                    <a href="tel:+1628900-3005" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('child-menu')}>
                                        <span className="menu-icon">
                                            <SVG src={baseUrl('media/icons/svg-icons/phone.svg')} />
                                        </span>
                                        <span className="menu-text">Call</span>
                                    </a>
                                </li>
                                <li className="submenu-nav-item">
                                    <a href="mailto:support@taggbox.com" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('child-menu')}>
                                        <span className="menu-icon">
                                            <SVG src={baseUrl('media/icons/svg-icons/mail.svg')} />
                                        </span>
                                        <span className="menu-text">Email</span>
                                    </a>
                                </li>
                                {/* <li className="submenu-nav-item">
                                    <Link to="/support" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('child-menu')}>
                                        <span className="menu-icon">
                                            <SVG src={baseUrl('media/icons/svg-icons/knowledge.svg')} />
                                        </span>
                                        <span className="menu-text">Knowledge</span>
                                    </Link>
                                </li> */}
                            </ul>
                        </Collapse>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <Link to={'/trash'} className={`nav-menu-item d-flex align-items-center fw-bold text-decoration-none ${menuActiveClass("trash")}`} activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <SVG src={baseUrl('media/icons/svg-icons/delete.svg')} />
                        </span>
                        <span className="menu-text">Trash</span>
                    </Link>
                </li>
            </ul>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {

    }
}

const mapDispatchToProps = dispatch => {
    return {
        resetFilter: () => dispatch(resetFilter()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AsideMenu);